// 把state放入mutations是为了方便开发，观察状态 ，和状态对应的相关方法

export const state = {
  // 客户信息
  customerInfo: null,
  // 全部新闻类型
  allNewsType: [],
  // 激活的菜单项
  activeMenu: 1
}

// 全局mutations
export const mutations = {
  updateCustomerInfo(state, info) {
    state.customerInfo = info
  },
  updateAllNewsType(state, allTypes) {
    state.allNewsType = allTypes
  },
  updateActiveMenu(state, active) {
    state.activeMenu = active
  }
}
